.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 220px;
  scroll-margin-top: 200px;

  &__title {
    font-weight: 600;
    font-size: 40px;
    color: white;
    text-align: center;
    font-family: "Nova Square", sans-serif;
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Nova Square", sans-serif;
    color: white;
  }

  &__input {
    width: 300px;
    height: 20px;
    color: white;
  }

  &__msg {
    width: 300px;
    height: 100px;
    color: white;
  }

  &__button {
    height: 50px;
    background-color: black;
    color: white;
    border: none;
    font-size: large;
    font-weight: 600;
    font-family: "Nova Square", sans-serif;
  }
}

@media (max-width: 768px) {
  .contact {
    scroll-margin-top: 270px;
  }
}

@media (max-width: 435px) {
  .contact {
    scroll-margin-top: 150px;
  }
}
